<script>
import MessageAlert from '@/components/MessageAlert'

export default {
  data () {
    return {
      isLoading: false,
      randoms: [],
      cart: [],
      screenWidth: document.documentElement.clientWidth
    }
  },
  components: { MessageAlert },
  methods: {
    copy (e) {
      e.currentTarget.select()
      document.execCommand('Copy')
      this.$bus.$emit('message:push', 'Ë§áË£ΩÊàêÂäü', 'dark')
    },
    randomProducts (msg) {
      const vm = this
      vm.isLoading = true
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/products/all`
      vm.$http.get(api).then((res) => {
        vm.isLoading = false
        if (res.data.success) {
          vm.randoms = []
          const products = res.data.products.filter(
            (item) => item.is_enabled === 1
          )
          const arr = products
          let ranNum = 3
          if (vm.screenWidth > 768) {
            ranNum = 4
          }
          for (let i = 0; i < ranNum; i++) {
            const ran = Math.floor(Math.random() * arr.length)
            vm.randoms.push(arr.splice(ran, 1)[0])
          }
          if (msg) {
            vm.$bus.$emit('message:push', msg, 'dark')
          }
        } else {
          vm.$bus.$emit('message:push', res.data.message, 'danger')
        }
        vm.isLoading = false
      })
    },
    addCart (id, num = 1) {
      const vm = this
      let rel = vm.cart.find((item) => item.product_id === id)
      let obj
      if (rel) {
        obj = { product_id: rel.product_id, qty: rel.qty + 1 }
        vm.isLoading = true
        const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/cart`
        vm.$http
          .post(api, {
            data: obj
          })
          .then((res) => {
            vm.isLoading = false
            if (res.data.success) {
              vm.delCart(rel.id, true)
              rel = ''
            } else {
              vm.$bus.$emit('message:push', res.data.message, 'danger')
            }
          })
      } else {
        obj = { product_id: id, qty: num }
        vm.isLoading = true
        const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/cart`
        vm.$http
          .post(api, {
            data: obj
          })
          .then((res) => {
            vm.isLoading = false
            if (res.data.success) {
              vm.getCart()
              vm.$bus.$emit('message:push', res.data.message, 'dark')
            } else {
              vm.$bus.$emit('message:push', res.data.message, 'danger')
            }
          })
      }
    },
    delCart (id, rep = false) {
      const vm = this
      vm.isLoading = true
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/cart/${id}`
      vm.$http.delete(api).then((res) => {
        if (res.data.success) {
          if (rep === false) {
            vm.$bus.$emit('message:push', res.data.message, 'dark')
          } else {
            vm.$bus.$emit('message:push', 'Ë≥ºÁâ©ËªäÊ∏ÖÂñÆÂ∑≤Êõ¥Êñ∞', 'dark')
          }
        } else {
          vm.$bus.$emit('message:push', res.data.message, 'danger')
        }
        vm.isLoading = false
        vm.getCart()
      })
    },
    getCart () {
      const vm = this
      vm.isLoading = true
      const api = `${process.env.VUE_APP_APIPATH}api/${process.env.VUE_APP_MYPATH}/cart`
      vm.$http.get(api).then((res) => {
        if (res.data.success) {
          vm.cart = res.data.data.carts
        } else {
          vm.$bus.$emit('message:push', res.data.message, 'danger')
        }
        vm.isLoading = false
      })
    }
  },
  created () {
    const vm = this
    vm.randomProducts('Ê≠°ËøéÂÖâËá®ÊãºÂúñËø∑ÔΩû‚ù§Ô∏è')
    vm.getCart()
  },
  watch: {
    $route: function (to, from) {
      if (to.path === '/' && from.path !== '/') {
        this.getCart()
      }
    }
  }
}
</script>
